/*Beams */
.beams-container {
  position: absolute;
  width: 100%;
  height: 100%;
}

/*Glass Surface*/
.glass-surface {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: opacity 0.26s ease-out;
  z-index: 100
}

.glass-surface__filter {
  width: 100%;
  height: 100%;
  pointer-events: none;
  position: absolute;
  inset: 0;
  opacity: 0;
  z-index: -1;
}

.glass-surface__content {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  border-radius: inherit;
  position: relative;
  z-index: 1;
}

.glass-surface--svg {
  background: light-dark(hsl(0 0% 100% / var(--glass-frost, 0)),
      hsl(0 0% 0% / var(--glass-frost, 0)));
  backdrop-filter: var(--filter-id, url(#glass-filter)) saturate(var(--glass-saturation, 1));
  box-shadow:
    0 0 2px 1px light-dark(color-mix(in oklch, black, transparent 85%),
      color-mix(in oklch, white, transparent 65%)) inset,
    0 0 10px 4px light-dark(color-mix(in oklch, black, transparent 90%),
      color-mix(in oklch, white, transparent 85%)) inset,
    0px 4px 16px rgba(17, 17, 26, 0.05),
    0px 8px 24px rgba(17, 17, 26, 0.05),
    0px 16px 56px rgba(17, 17, 26, 0.05),
    0px 4px 16px rgba(17, 17, 26, 0.05) inset,
    0px 8px 24px rgba(17, 17, 26, 0.05) inset,
    0px 16px 56px rgba(17, 17, 26, 0.05) inset;
}

.glass-surface--fallback {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(12px) saturate(1.8) brightness(1.1);
  -webkit-backdrop-filter: blur(12px) saturate(1.8) brightness(1.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow:
    0 8px 32px 0 rgba(31, 38, 135, 0.2),
    0 2px 16px 0 rgba(31, 38, 135, 0.1),
    inset 0 1px 0 0 rgba(255, 255, 255, 0.4),
    inset 0 -1px 0 0 rgba(255, 255, 255, 0.2);
}

@media (prefers-color-scheme: dark) {
  .glass-surface--fallback {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(12px) saturate(1.8) brightness(1.2);
    -webkit-backdrop-filter: blur(12px) saturate(1.8) brightness(1.2);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow:
      inset 0 1px 0 0 rgba(255, 255, 255, 0.2),
      inset 0 -1px 0 0 rgba(255, 255, 255, 0.1);
  }
}

@supports not (backdrop-filter: blur(10px)) {
  .glass-surface--fallback {
    background: rgba(255, 255, 255, 0.4);
    box-shadow:
      inset 0 1px 0 0 rgba(255, 255, 255, 0.5),
      inset 0 -1px 0 0 rgba(255, 255, 255, 0.3);
  }

  .glass-surface--fallback::before {
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(255, 255, 255, 0.15);
    border-radius: inherit;
    z-index: -1;
  }
}

@supports not (backdrop-filter: blur(10px)) {
  @media (prefers-color-scheme: dark) {
    .glass-surface--fallback {
      background: rgba(0, 0, 0, 0.4);
    }

    .glass-surface--fallback::before {
      background: rgba(255, 255, 255, 0.05);
    }
  }
}

.glass-surface:focus-visible {
  outline: 2px solid light-dark(#007AFF, #0A84FF);
  outline-offset: 2px;
}

/*Snackbar*/

.snackbar {
  visibility: hidden;
  opacity: 0;
  z-index: 9999;

  position: fixed;
  bottom: -60px; /*You can change this carefully! Don't forget to change the bottom property on the .snackbar.show as well.*/
  right: 15px;  /*Positioned 15px from right by default. Change this based on yuor needs.*/

  width: 300px; /*Changable.*/
  height: 45px; /*Changable*/

  padding: 5px 10px 5px 15px; /*Changable*/

  background-color: var(--secondary); /*Changable*/
  box-shadow: 0px 0px 16px -2px #000000; /*Optional, changable*/
  color: #F5F5F5;

  border-radius: 15px; /*Changable*/

  font-size: 17px; /*Changable*/
  font-weight: 500; /*Optional, changable*/

  display: grid;
  align-items: center; /*Text is positioned center VERTICALLY by default. Optional, changable.*/

  filter: blur(15px);

  transition: 0.5s cubic-bezier(.38,.09,.24,.98);
}

.snackbar.show {
  visibility: visible;
  opacity: 1;

  bottom: 15px; /*You can change this carefully!*/

  filter: blur(0px);
}
  

/*SYSTEM*/

#contact-maindiv {
  width: calc(100svw - 120px);
  height: calc(80svh - 120px);
  max-width: 350px;
  max-height: 500px;
  min-height: 400px;

  background-color: transparent;

  position: relative;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);

  border-radius: 30px;

  overflow: hidden;
  padding: 30px;

  margin-bottom: calc(min(220px, 100svw - 120px))
}

#contact-title  {
  text-align: center;
  width: 270px;

  background-color: transparent;
  
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

#contact-form {
  width: 100%;
  height: calc(100% - 40px - 60px);

  background-color: transparent;

  position: relative;
  top: 30px
}

#contact-form input {
  width: 100%;
  height: 100%;

  border-radius: 30px;
  border: none;
  outline: none;

  padding: 0px 20px 0px 20px;

  background-color: transparent;

  color: var(--font-primary);
  font-family: _semibold;
  font-size: 17px;
}

#contact-form textarea {
  width: 100%;
  height: 100%;

  border-radius: 30px;
  border: none;
  outline: none;

  padding: 40px 20px 0px 20px;

  background-color: transparent;

  color: var(--font-primary);
  font-family: _semibold;
  font-size: 17px;
}

#contact-form input::placeholder,
#contact-form textarea::placeholder {
  filter: drop-shadow(0px 0px 5px var(--primary));
}

#contact-input-submit {
  width: 100%;
  height: 50px;

  border-radius: 30px;
  border: none;

  color: var(--font-primary);
  font-family: _semibold;
  font-size: 17px;

  background-color: var(--primary-transparent);
  backdrop-filter: blur(30px);

  cursor: pointer
}

@media screen and (min-width: 1000px) {
  #contact-maindiv {
    width: calc(80svw - 120px);
    height: calc(65svh - 120px);
    max-width: 1000px;
    max-height: 400px;

    background-color: transparent;

    position: relative;
    top: calc(0px + min(700px, 65svh + 30px + 100px));
    left: 50%;
    transform: translateX(-50%);

    border-radius: 30px;

    overflow: hidden;
    padding: 60px;

    margin-bottom: calc(min(800px, 70svw))
  }

    
  #contact-title  {
    text-align: left;
    width: calc(50% - 25px);
    max-width: 120px;

    background-color: transparent;
    
    position: absolute;
    left: calc(0px + clamp(60px, 4vw, 100px));
    top: 50%;
    transform: translateY(-50%);

    font-family: _bold;
    font-size: calc(clamp(40px, 3vw, 50px));
  }

  #contact-form {
    width: 45%;
    max-width: 500px;
    min-width: 400px;
    height: calc(max(calc(65svh - 120px), 4vw) - 120px);
    min-height: 60%;

    background-color: transparent;

    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: calc(0px + clamp(60px, 4vw, 100px));

    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;
    column-gap: 20px;
  }

  #contact-form input {
    width: 100%;
    height: 100%;

    border-radius: 30px;
    border: none;
    outline: none;

    padding: 0px 10px 0px 20px;

    background-color: transparent;

    color: var(--font-primary);
    font-family: _semibold;
    font-size: 17px;
  }

  #contact-form textarea {
    width: 100%;
    height: 100%;

    border-radius: 30px;
    border: none;
    outline: none;

    padding: 40px 20px 0px 20px;

    background-color: transparent;

    color: var(--font-primary);
    font-family: _semibold;
    font-size: 17px;
  }

  #contact-form input::placeholder,
  #contact-form textarea::placeholder {
    filter: drop-shadow(0px 0px 5px var(--primary));
  }

  #contact-input-submit {
      width: 100%;
      height: 50px;

      border-radius: 30px;
      border: none;

      color: var(--font-primary);
      font-family: _semibold;
      font-size: 17px;

      background-color: var(--primary-transparent);
      backdrop-filter: blur(30px);
  }
}